import { createContext, FC, useContext, useMemo, useState } from "react";

import allowedVerticalsData from "Constants/Data/allowed-verticals.json";
import { WaAllowedVertical } from "Shared/Interfaces/WhatsApp";

import {
  WaBasicSettingsApiContextState as WA_BSA_CS,
  WaBasicSettingsDataContextState as WA_BSD_CS,
} from "./types";

const WaBasicSettingsData = createContext<WA_BSD_CS | undefined>(undefined);

const WaBasicSettingsApi = createContext<WA_BSA_CS | undefined>(undefined);

const WaBasicSettingsProvider: FC<any> = ({ children }) => {
  const allowedVerticals: WaAllowedVertical[] = allowedVerticalsData;
  const [chatbotNumber, setChatBotNumber] = useState<number | undefined>();
  const data: WA_BSD_CS = {
    allowedVerticals,
    chatbotNumber,
  };
  const api = useMemo<WA_BSA_CS>(() => ({ setChatBotNumber }), []);

  return (
    <WaBasicSettingsData.Provider value={data}>
      <WaBasicSettingsApi.Provider value={api}>
        {children}
      </WaBasicSettingsApi.Provider>
    </WaBasicSettingsData.Provider>
  );
};

export const useWaBasicSettingsData = () => {
  const context = useContext(WaBasicSettingsData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of WaBasicSettingsProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useWaBasicSettingsApi = () => {
  const context = useContext(WaBasicSettingsApi);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of WaBasicSettingsProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default WaBasicSettingsProvider;
