import { Outlet } from "react-router-dom";

import { GlobalDataProvider, AppDataProvider } from "Contexts/App";
import { SnackProvider } from "Contexts/Snack";
import ErrorBoundary from "Hoc/ErrorBoundary";
import { SnackbarProvider } from "notistack";

const RootLayout = () => {
  return (
    <ErrorBoundary>
      <GlobalDataProvider>
        <SnackbarProvider maxSnack={3}>
          <SnackProvider>
            <AppDataProvider>
              <Outlet />
            </AppDataProvider>
          </SnackProvider>
        </SnackbarProvider>
      </GlobalDataProvider>
    </ErrorBoundary>
  );
};

export default RootLayout;
