import { Box } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Theme } from "@mui/system";

const useStyles = makeStyles((theme: Theme) => ({
  "@keyframes ripple": {
    "0%": { top: 36, left: 36, width: 0, height: 0, opacity: 1 },
    "100%": { top: 0, left: 0, width: 72, height: 72, opacity: 0 },
  },

  [theme.breakpoints.up("xs")]: {
    loader: {
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      width: "100%",
      height: "100vh",
      zIndex: 11111,
    },

    loading: {
      padding: 0,
      margin: 0,
      width: "100%",
      height: "100%",
      zIndex: 11111,
      position: "absolute",
      top: 0,

      "& .ripples": {
        left: "50%",
        top: "50%",
        transform: "translate(-50%, -50%)",
        display: "inline-block",
        position: "relative",
        width: 90,
        height: 90,

        "& .ripple": {
          position: "absolute",
          border: `4px solid ${theme.palette.primary.main}`,
          opacity: 1,
          borderRadius: "50%",
          animation: "$ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite",

          "&:nth-child(2)": {
            animationDelay: "-0.5s",
          },
        },
      },
    },
  },
}));

const Loader = () => {
  const { loader, loading } = useStyles();

  return (
    <Box className={loader}>
      <Box className={loading}>
        <Box className="ripples">
          <Box component="span" className="ripple" />
          <Box component="span" className="ripple" />
        </Box>
      </Box>
    </Box>
  );
};

export default Loader;
