import {
  createContext,
  ReactNode,
  useCallback,
  useContext,
  useEffect,
  useMemo,
} from "react";
import { useNavigate } from "react-router-dom";
import { useMediaQuery, useTheme } from "@mui/material";
import { Theme } from "@mui/system";

import { useLocalStorage } from "Hooks/useStorage";

import { GlobalDataType } from "./types";

const GlobalData = createContext<GlobalDataType | undefined>(undefined);

const GlobalDataProvider = ({ children }: { children: ReactNode }) => {
  const navigate = useNavigate();
  const [appVer, setAppVer] = useLocalStorage("appVer", "");
  const theme = useTheme();
  false && console.log(theme);
  const isMobile = useMediaQuery((theme: Theme) =>
    theme.breakpoints.down("md")
  );
  const isTablet = useMediaQuery((theme: Theme) => theme.breakpoints.up("md"));
  const isXlScreen = useMediaQuery((theme: Theme) =>
    theme.breakpoints.up("xl")
  );
  const isBelowDesk = useMediaQuery((theme: Theme) =>
    theme.breakpoints.down("lg")
  );
  const [businessId, setBusinessId] = useLocalStorage("businessId");
  const [accessToken, setAccessToken] = useLocalStorage("token");
  const [callingCode, setCallingCode] = useLocalStorage("callingCode");
  const [country, setCountry] = useLocalStorage("country");
  const [language, setLanguage] = useLocalStorage("lang");
  const [lng, setLng] = useLocalStorage("lng");
  const [lat, setLat] = useLocalStorage("lat");
  const doNavigate = (path: string) => {
    if (navigate) {
      navigate(path);
    } else {
      window.location.href = path;
    }
  };
  const getCountry = useCallback(async () => {
    try {
      if (country && callingCode && language && lng && lat) {
        return;
      }
      const visitorReq = await fetch(
        `//api.ipregistry.co/?key=${process.env.REACT_APP_IP_REG_KEY}`,
        {
          method: "GET",
        }
      );
      if (visitorReq.ok) {
        const visitor = await visitorReq.json();
        if (visitor) {
          const { location } = visitor;
          setCallingCode(location.country.calling_code);
          setCountry(location.country.code);
          setLanguage(location.language.code);
          setLng(location.longitude);
          setLat(location.latitude);
        }
      }
    } catch (error) {
      console.error(error);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [setCountry]);
  const setVersion = useCallback(async () => {
    appVer === "" && setAppVer(process.env.REACT_APP_VER);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const api = useMemo<GlobalDataType>(
    () => ({
      navigate,
      appVer,
      theme,
      isMobile,
      isTablet,
      isXlScreen,
      isBelowDesk,
      accessToken,
      businessId,
      callingCode,
      country,
      doNavigate,
      setAccessToken,
      setBusinessId,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [setAccessToken, setBusinessId]
  );

  useEffect(() => {
    setVersion();
  }, [setVersion]);

  useEffect(() => {
    getCountry();
  }, [getCountry]);

  return <GlobalData.Provider value={api}>{children}</GlobalData.Provider>;
};

export const useGlobal = () => {
  const context = useContext(GlobalData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of GlobalDataProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default GlobalDataProvider;
