import { createTheme } from "@mui/material/styles";

const { palette } = createTheme({
  palette: {
    mode: "light",
    primary: {
      contrastText: "#ffffff",
      dark: "#32C5FF",
      light: "#abddff",
      main: "#12B0EE",
    },
    secondary: {
      main: "#BEBEBE",
      contrastText: "#ffffff",
    },
    error: {
      main: "#d32f2f",
      light: "#ef5350",
      dark: "#c62828",
      contrastText: "#ffffff",
    },
    success: {
      contrastText: "#ffffff",
      dark: "#1b5e20",
      light: "#4caf50",
      main: "#12B347",
    },
    info: {
      contrastText: "#ffffff",
      dark: "#01579b",
      light: "#03a9f4",
      main: "#0288d1",
    },
    warning: {
      main: "#ed6c02",
      light: "#ff9800",
      dark: "#e65100",
      contrastText: "#ffffff",
    },
    text: {
      primary: "#24272C",
      secondary: "#24272CB3",
      disabled: "#24272C80",
    },
  },
});

const theme = createTheme({
  palette,
  components: {
    MuiSwitch: {
      styleOverrides: {
        root: {
          padding: 6,
        },
        colorPrimary: {
          "&.Mui-checked": {
            color: "#ffffff",
          },
        },
        track: {
          borderRadius: 13,
          backgroundColor: "#E9E9EA",
          opacity: 1,

          ".Mui-checked.Mui-checked + &": {
            backgroundColor: palette.primary.dark,
            opacity: 1,
          },
        },
      },
    },
    MuiButton: {
      defaultProps: {
        variant: "contained",
        color: "primary",
        disableElevation: true,
        size: "medium",
      },
      styleOverrides: {
        root: {
          borderRadius: "8px",
          textTransform: "unset",
          letterSpacing: 1,
          fontStyle: "normal",
          justifyContent: "start",
          width: "max-content",
          boxShadow: "unset",
        },
      },
    },
    MuiInput: {
      styleOverrides: {
        root: {
          borderRadius: 8,
          minHeight: 52,
          overflow: "hidden",
          width: "100%",

          "&.MuiFilledInput-root": {
            backgroundColor: "#f2f3f7",
            paddingLeft: "5%",
          },

          "&::before, &:after": {
            display: "none",
          },
        },
      },
    },
    MuiTextField: {
      defaultProps: {
        variant: "filled",
      },
      styleOverrides: {
        root: ({ ownerState: { multiline, select, variant } }) => ({
          width: "100%",
          ...(variant === "filled" && {
            "& .MuiFormLabel-root.Mui-error": {
              color: "#f44336 !important",
            },

            "& .MuiInputLabel-root": {
              color: "#8695AC !important",
            },

            // borderRadius: "8px",
            "& .MuiFilledInput-root": {
              ...(!select &&
                multiline && {
                  padding: "4%",
                }),

              "&:not()": {},

              backgroundColor: "#F2F3F7",
              borderRadius: 8,

              "&.Mui-disabled": {
                backgroundColor: "inherit",
              },

              "&::before, &::after": {
                display: "none",
              },

              "&:not(.MuiInputBase-multiline)": {
                minHeight: 52,
                overflow: "hidden",
              },
            },

            "& .MuiFilledInput-underline:before": {
              borderBottomColor: "transparent",
            },

            "& .MuiFilledInput-underline:after": {
              borderBottomColor: "transparent",
            },

            "& .MuiOutlinedInput-root": {
              "& fieldset": {
                borderColor: "red",
              },

              "&:hover fieldset": {
                borderColor: "yellow",
              },

              "&.Mui-focused fieldset": {
                borderColor: "#8695AC",
              },
            },
          }),
        }),
      },
    },
    MuiListItemIcon: {
      styleOverrides: {
        root: {
          justifyContent: "center",
          minWidth: "unset",
        },
      },
    },
  },
});

export default theme;
