import { FC } from "react";
import { Outlet } from "react-router-dom";

import WaBasicSettingsProvider from "./WaBasicSettingsProvider";
import WATemplateProvider from "./WATemplateProvider";

const WhatsAppProvider: FC<any> = ({ children }) => {
  return (
    <WaBasicSettingsProvider>
      <WATemplateProvider>
        <Outlet />
      </WATemplateProvider>
    </WaBasicSettingsProvider>
  );
};

export default WhatsAppProvider;
