import { createContext, ReactNode, useContext, useMemo } from "react";
import {
  SnackbarKey,
  SnackbarOrigin,
  useSnackbar,
  VariantType,
} from "notistack";
import { IconButton, Slide } from "@mui/material";
import { TransitionProps } from "@mui/material/transitions";
import { Close } from "@mui/icons-material";

import { SnackActionContextState } from "./types";
import { useGlobal } from "Contexts/App";

const SnackAction = createContext<SnackActionContextState | undefined>(
  undefined
);

const SnackProvider = ({ children }: { children: ReactNode }) => {
  const { isMobile } = useGlobal();
  const { enqueueSnackbar, closeSnackbar } = useSnackbar();
  const action = (snackbarId: SnackbarKey) => (
    <IconButton onClick={() => closeSnackbar(snackbarId)}>
      <Close sx={{ color: "#FFFFFF" }} />
    </IconButton>
  );
  const anchorOrigin: SnackbarOrigin = isMobile
    ? { horizontal: "center", vertical: "bottom" }
    : { horizontal: "right", vertical: "top" };
  const TransitionComponent = (
    props: TransitionProps & {
      children: React.ReactElement<any, any>;
    }
  ) => <Slide {...props} direction={isMobile ? "up" : "down"} />;

  const SnackActions = useMemo<SnackActionContextState>(
    () => ({
      setErrorMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "error"
      ) =>
        enqueueSnackbar(message, {
          anchorOrigin,
          action,
          variant,
          autoHideDuration,
          TransitionComponent,
        }),
      setInfoMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "info"
      ) =>
        enqueueSnackbar(message, {
          anchorOrigin,
          action,
          variant,
          autoHideDuration,
          TransitionComponent,
        }),
      setSuccessMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "success"
      ) =>
        enqueueSnackbar(message, {
          anchorOrigin,
          action,
          variant,
          autoHideDuration,
          TransitionComponent,
        }),
      setWarningMessage: (
        message: string,
        autoHideDuration: number | undefined = 5000,
        variant: VariantType = "warning"
      ) =>
        enqueueSnackbar(message, {
          anchorOrigin,
          action,
          variant,
          autoHideDuration,
          TransitionComponent,
        }),
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <SnackAction.Provider value={SnackActions}>{children}</SnackAction.Provider>
  );
};

export const useSnacks = () => {
  const context = useContext(SnackAction);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SnackProvider, otherwise it will not function correctly."
    );
  }
  return context;
};

export default SnackProvider;
