import { createContext, ReactNode, useContext, useState } from "react";

import { WORKSPACE, WORKSPACE_LABEL } from "Shared/Enums/WorkSpace";

import { AppDataType } from "./types";

const AppData = createContext<AppDataType | undefined>(undefined);

const AppDataProvider = ({ children }: { children: ReactNode }) => {
  const [workSpace, setWorkSpace] = useState<WORKSPACE>(WORKSPACE.ENGAGE);
  const [headerContent, setHeaderContent] = useState<ReactNode>(null);
  const workSpaceLabel: WORKSPACE_LABEL =
    workSpace === WORKSPACE.ENGAGE
      ? WORKSPACE_LABEL.ENGAGE
      : WORKSPACE_LABEL.CURATE;
  const changeWorkSpace = (newWorkSpace: WORKSPACE) =>
    workSpace !== newWorkSpace && setWorkSpace(newWorkSpace);

  return (
    <AppData.Provider
      value={{
        workSpace,
        setWorkSpace,
        headerContent,
        setHeaderContent,
        workSpaceLabel,
        changeWorkSpace,
      }}
    >
      {children}
    </AppData.Provider>
  );
};

export const useAppState = () => {
  const context = useContext(AppData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of AppDataProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default AppDataProvider;
