import { createContext, FC, useContext, useMemo, useState } from "react";

import {
  WATemplateDataInterface,
  WATemplateCategory,
  WATemplateLanguage,
  WATemplateType,
  WATemplateHeaderType,
  WATemplateButtonType,
  WATemplateButtonCtaType,
} from "Shared/Interfaces/WhatsApp";
import waTemplateCategoryData from "Constants/Data/wa-template-categories.json";
import waTemplateLanguagesData from "Constants/Data/languages.json";

import { WAApiContextState, WADataContextState } from "./types";

const WAData = createContext<WADataContextState | undefined>(undefined);

const WAApi = createContext<WAApiContextState | undefined>(undefined);

const WATemplateProvider: FC<any> = ({ children }) => {
  const [waTemplateFilter, setWaTemplateFilter] = useState<string>("");
  const [totalWaTemplates, setTotalWaTemplates] = useState<number>(0);
  const waTemplateCategories: WATemplateCategory[] = waTemplateCategoryData;
  const waTemplateLanguages: WATemplateLanguage[] = waTemplateLanguagesData;
  const waTemplateTypes: WATemplateType[] = [
    {
      value: "text",
      label: "Text only",
    },
    {
      value: "media",
      label: "Media & Interactive",
    },
  ];
  const waTemplateHeaderTypes: WATemplateHeaderType[] = [
    {
      name: "Text",
      value: "TEXT",
    },
    {
      name: "Image",
      value: "IMAGE",
    },
    {
      name: "Video",
      value: "VIDEO",
    },
    {
      name: "Documents",
      value: "DOCUMENT",
    },
  ];
  const waTemplateButtonTypes: WATemplateButtonType[] = [
    {
      name: "CALL_TO_ACTION",
      label: "Call to Action",
    },
    {
      name: "QUICK_REPLY",
      label: "Quick Reply",
    },
  ];
  const waTemplateButtonCtaTypes: WATemplateButtonCtaType[] = [
    {
      name: "URL",
      value: "URL",
    },
    {
      name: "Phone Number",
      value: "PHONE_NUMBER",
    },
  ];
  const [waTemplatesData, setWaTemplatesData] = useState<
    WATemplateDataInterface[]
  >([]);
  const [editWaTemplateId, setEditWaTemplateId] = useState<
    number | undefined
  >();
  const [previewWaTemplateId, setPreviewWaTemplateId] = useState<
    number | undefined
  >();
  const [showWaTemplateForm, setShowWaTemplateForm] = useState<boolean>(false);
  const [previewWaTemplateData, setPreviewWaTemplateData] = useState<
    any | null
  >(null);
  const [showWaPreviewForm, setShowWaPreviewForm] = useState<boolean>(false);
  const [showWaTemplatePreview, setShowWaTemplatePreview] =
    useState<boolean>(false);
  const data: WADataContextState = {
    waTemplateFilter,
    totalWaTemplates,
    waTemplateCategories,
    waTemplateLanguages,
    waTemplateTypes,
    waTemplateHeaderTypes,
    waTemplateButtonTypes,
    waTemplatesData,
    editWaTemplateId,
    previewWaTemplateId,
    showWaTemplateForm,
    previewWaTemplateData,
    showWaPreviewForm,
    showWaTemplatePreview,
    waTemplateButtonCtaTypes,
  };
  const api = useMemo<WAApiContextState>(
    () => ({
      setWaTemplateFilter,
      setTotalWaTemplates,
      setWaTemplatesData,
      setEditWaTemplateId,
      setPreviewWaTemplateId,
      setShowWaTemplateForm,
      setPreviewWaTemplateData,
      setShowWaPreviewForm,
      setShowWaTemplatePreview,
    }),
    []
  );

  return (
    <WAData.Provider value={data}>
      <WAApi.Provider value={api}>{children}</WAApi.Provider>
    </WAData.Provider>
  );
};

export const useWaTemplateData = () => {
  const context = useContext(WAData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of WAProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useWaTemplateApi = () => {
  const context = useContext(WAApi);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of WAProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default WATemplateProvider;
