import { lazy } from "react";
import { useRoutes } from "react-router-dom";

import RootLayout from "Containers/Root/Layout";
import PrivateRouteWrapper from "SharedComponents/PrivateRouteWrapper";
import PublicRouteWrapper from "SharedComponents/PublicRouteWrapper";
import { WhatsAppProvider } from "Contexts/WhatsApp";

const BusinessSettings = lazy(() => import("Pages/BusinessSettings"));
const BusinessSettingsLayout = lazy(
  () => import("Containers/BusinessSettings/Layout")
);
const AppLayout = lazy(() => import("Containers/App/Layout"));
const GeneralSettings = lazy(
  () => import("Pages/BusinessSettings/GeneralSettings")
);
const TeamMembers = lazy(() => import("Pages/BusinessSettings/TeamMembers"));
const LeadDataFields = lazy(
  () => import("Pages/BusinessSettings/LeadDataFields")
);
const LeadDistRules = lazy(
  () => import("Pages/BusinessSettings/LeadDistRules")
);
const WABasicSettings = lazy(
  () => import("Pages/BusinessSettings/WABasicSettings")
);
const WAUsageReports = lazy(
  () => import("Pages/BusinessSettings/WAUsageReports")
);
const WATemplateMessages = lazy(
  () => import("Pages/BusinessSettings/WATemplateMessages")
);
const SocialMediaLinking = lazy(
  () => import("Pages/BusinessSettings/SocialMediaLinking")
);
const Engage = lazy(() => import("Pages/Engage"));
const Chat = lazy(() => import("Pages/Engage/Chat"));
const Broadcast = lazy(() => import("Pages/Engage/Broadcast"));
const ChatFlowLayout = lazy(
  () => import("Containers/ChatFlowDashboard/Layout")
);
const ChatFlowDashboard = lazy(() => import("Pages/ChatFlowDashboard"));
const Notfound = lazy(() => import("Pages/404"));

const Routes = () =>
  useRoutes([
    {
      element: <RootLayout />,
      children: [
        {
          element: <PrivateRouteWrapper />,
          children: [
            {
              path: "/engage",
              element: <AppLayout />,
              children: [
                {
                  index: true,
                  element: <Engage />,
                },
                {
                  path: "chat",
                  element: <Chat />,
                },
                {
                  path: "broadcast",
                  element: <Broadcast />,
                },
              ],
            },
            {
              path: "/business-settings",
              element: <BusinessSettings />,
            },
            {
              path: "/business",
              element: <BusinessSettingsLayout />,
              children: [
                {
                  index: true,
                  element: <BusinessSettings />,
                },
                {
                  path: "general-settings",
                  element: <GeneralSettings />,
                },
                {
                  path: "team-members",
                  element: <TeamMembers />,
                },
                {
                  path: "lead-data-fields",
                  element: <LeadDataFields />,
                },
                {
                  path: "lead-distribution-rules",
                  element: <LeadDistRules />,
                },
                {
                  path: "wa",
                  element: <WhatsAppProvider />,
                  children: [
                    {
                      path: "basic-settings",
                      element: <WABasicSettings />,
                    },
                    {
                      path: "usage-report",
                      element: <WAUsageReports />,
                    },
                    {
                      path: "template-messages",
                      element: <WATemplateMessages />,
                    },
                  ],
                },
                {
                  path: "social-media-linking",
                  element: <SocialMediaLinking />,
                },
              ],
            },
            {
              path: "/chat-flow",
              element: <ChatFlowLayout />,
              children: [
                {
                  index: true,
                  element: <ChatFlowDashboard />,
                },
                {
                  path: ":chatflow_id",
                  element: <ChatFlowDashboard />,
                },
              ],
            },
          ],
        },
        {
          element: <PublicRouteWrapper />,
          children: [{ path: "/login", element: <h1>Login</h1> }],
        },
        { path: "/", element: <h1>Home</h1> },
        { path: "*", element: <Notfound /> },
      ],
    },
  ]);

export default Routes;
